*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  height: 100%; }

body {
  display: flex;
  flex-direction: column;
  margin: 0;
  font-weight: 400;
  font-size: 15px;
  line-height: 21px;
  font-family: "Roboto", "Arial", sans-serif;
  color: #000000; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

a:hover {
  opacity: 0.4; }

input::placeholder {
  overflow: visible; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  /* 1 */
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%); }

.container {
  width: 100%;
  background-color: #000000;
  max-width: 1050px;
  padding-right: 12px;
  padding-left: 12px;
  margin-right: auto;
  margin-left: auto; }

@media (max-width: 1023px) {
  .container {
    padding-right: 25px;
    padding-left: 25px; } }

@media (max-width: 740px) {
  .container {
    background-color: #77b900;
    padding-right: 15px;
    padding-left: 15px; } }

h1 {
  color: red; }
